"use client";

import { useEffect, useMemo, useCallback, useState } from "react";
import {
  useCreateBlockNote,
  SideMenu as BlockSideMenu,
  SideMenuController,
  DragHandleButton,
  SuggestionMenuController,
  getDefaultReactSlashMenuItems,
} from "@blocknote/react";
import { BlockNoteView } from "@blocknote/mantine";
import { cn } from "../utils/cn";

import type {
  DefaultPartialBlock,
  LumirEditorProps,
  DefaultBlockSchema,
  DefaultInlineContentSchema,
  DefaultStyleSchema,
} from "../types";

import { createS3Uploader } from "../utils/s3-uploader";

// ==========================================
// Ïú†Ìã∏Î¶¨Ìã∞ ÌÅ¥ÎûòÏä§Îì§
// ==========================================

/**
 * ÏΩòÌÖêÏ∏† Í¥ÄÎ¶¨ Ïú†Ìã∏Î¶¨Ìã∞
 * Í∏∞Î≥∏ Î∏îÎ°ù ÏÉùÏÑ± Î∞è ÏΩòÌÖêÏ∏† Í≤ÄÏ¶ù Î°úÏßÅÏùÑ Îã¥Îãπ
 */
export class ContentUtils {
  /**
   * JSON Î¨∏ÏûêÏó¥Ïùò Ïú†Ìö®ÏÑ±ÏùÑ Í≤ÄÏ¶ùÌï©ÎãàÎã§
   * @param jsonString Í≤ÄÏ¶ùÌï† JSON Î¨∏ÏûêÏó¥
   * @returns Ïú†Ìö®Ìïú JSON Î¨∏ÏûêÏó¥Ïù∏ÏßÄ Ïó¨Î∂Ä
   */
  static isValidJSONString(jsonString: string): boolean {
    try {
      const parsed = JSON.parse(jsonString);
      return Array.isArray(parsed);
    } catch {
      return false;
    }
  }

  /**
   * JSON Î¨∏ÏûêÏó¥ÏùÑ DefaultPartialBlock Î∞∞Ïó¥Î°ú ÌååÏã±Ìï©ÎãàÎã§
   * @param jsonString JSON Î¨∏ÏûêÏó¥
   * @returns ÌååÏã±Îêú Î∏îÎ°ù Î∞∞Ïó¥ ÎòêÎäî null (ÌååÏã± Ïã§Ìå® Ïãú)
   */
  static parseJSONContent(jsonString: string): DefaultPartialBlock[] | null {
    try {
      const parsed = JSON.parse(jsonString);
      if (Array.isArray(parsed)) {
        return parsed as DefaultPartialBlock[];
      }
      return null;
    } catch {
      return null;
    }
  }

  /**
   * Í∏∞Î≥∏ paragraph Î∏îÎ°ù ÏÉùÏÑ±
   * @returns Í∏∞Î≥∏ ÏÑ§Ï†ïÏù¥ Ï†ÅÏö©Îêú DefaultPartialBlock
   */
  static createDefaultBlock(): DefaultPartialBlock {
    return {
      type: "paragraph",
      props: {
        textColor: "default",
        backgroundColor: "default",
        textAlignment: "left",
      },
      content: [{ type: "text", text: "", styles: {} }],
      children: [],
    };
  }

  /**
   * ÏΩòÌÖêÏ∏† Ïú†Ìö®ÏÑ± Í≤ÄÏ¶ù Î∞è Í∏∞Î≥∏Í∞í ÏÑ§Ï†ï
   * @param content ÏÇ¨Ïö©Ïûê Ï†úÍ≥µ ÏΩòÌÖêÏ∏† (Í∞ùÏ≤¥ Î∞∞Ïó¥ ÎòêÎäî JSON Î¨∏ÏûêÏó¥)
   * @param emptyBlockCount Îπà Î∏îÎ°ù Í∞úÏàò (Í∏∞Î≥∏Í∞í: 3)
   * @returns Í≤ÄÏ¶ùÎêú ÏΩòÌÖêÏ∏† Î∞∞Ïó¥
   */
  static validateContent(
    content?: DefaultPartialBlock[] | string,
    emptyBlockCount: number = 3
  ): DefaultPartialBlock[] {
    // 1. Î¨∏ÏûêÏó¥Ïù∏ Í≤ΩÏö∞ JSON ÌååÏã± ÏãúÎèÑ
    if (typeof content === "string") {
      if (content.trim() === "") {
        return this.createEmptyBlocks(emptyBlockCount);
      }

      const parsedContent = this.parseJSONContent(content);
      if (parsedContent && parsedContent.length > 0) {
        return parsedContent;
      }

      // ÌååÏã± Ïã§Ìå® Ïãú Îπà Î∏îÎ°ù ÏÉùÏÑ±
      return this.createEmptyBlocks(emptyBlockCount);
    }

    // 2. Î∞∞Ïó¥Ïù∏ Í≤ΩÏö∞ Í∏∞Ï°¥ Î°úÏßÅ
    if (!content || content.length === 0) {
      return this.createEmptyBlocks(emptyBlockCount);
    }

    return content;
  }

  /**
   * Îπà Î∏îÎ°ùÎì§ÏùÑ ÏÉùÏÑ±Ìï©ÎãàÎã§
   * @param emptyBlockCount ÏÉùÏÑ±Ìï† Î∏îÎ°ù Í∞úÏàò
   * @returns ÏÉùÏÑ±Îêú Îπà Î∏îÎ°ù Î∞∞Ïó¥
   */
  private static createEmptyBlocks(
    emptyBlockCount: number
  ): DefaultPartialBlock[] {
    return Array.from({ length: emptyBlockCount }, () =>
      this.createDefaultBlock()
    );
  }
}

/**
 * ÏóêÎîîÌÑ∞ ÏÑ§Ï†ï Í¥ÄÎ¶¨ Ïú†Ìã∏Î¶¨Ìã∞
 * Í∞ÅÏ¢Ö ÏÑ§Ï†ïÏùò Í∏∞Î≥∏Í∞íÍ≥º Í≤ÄÏ¶ù Î°úÏßÅÏùÑ Îã¥Îãπ
 */
export class EditorConfig {
  /**
   * ÌÖåÏù¥Î∏î ÏÑ§Ï†ï Í∏∞Î≥∏Í∞í Ï†ÅÏö©
   * @param userTables ÏÇ¨Ïö©Ïûê ÌÖåÏù¥Î∏î ÏÑ§Ï†ï
   * @returns Í∏∞Î≥∏Í∞íÏù¥ Ï†ÅÏö©Îêú ÌÖåÏù¥Î∏î ÏÑ§Ï†ï
   */
  static getDefaultTableConfig(userTables?: LumirEditorProps["tables"]) {
    return {
      splitCells: userTables?.splitCells ?? true,
      cellBackgroundColor: userTables?.cellBackgroundColor ?? true,
      cellTextColor: userTables?.cellTextColor ?? true,
      headers: userTables?.headers ?? true,
    };
  }

  /**
   * Ìó§Îî© ÏÑ§Ï†ï Í∏∞Î≥∏Í∞í Ï†ÅÏö©
   * @param userHeading ÏÇ¨Ïö©Ïûê Ìó§Îî© ÏÑ§Ï†ï
   * @returns Í∏∞Î≥∏Í∞íÏù¥ Ï†ÅÏö©Îêú Ìó§Îî© ÏÑ§Ï†ï
   */
  static getDefaultHeadingConfig(userHeading?: LumirEditorProps["heading"]) {
    return userHeading?.levels && userHeading.levels.length > 0
      ? userHeading
      : { levels: [1, 2, 3, 4, 5, 6] as (1 | 2 | 3 | 4 | 5 | 6)[] };
  }

  /**
   * ÎπÑÌôúÏÑ±ÌôîÌï† ÌôïÏû• Í∏∞Îä• Î™©Î°ù ÏÉùÏÑ±
   * @param userExtensions ÏÇ¨Ïö©Ïûê Ï†ïÏùò ÎπÑÌôúÏÑ± ÌôïÏû•
   * @param allowVideo ÎπÑÎîîÏò§ ÏóÖÎ°úÎìú ÌóàÏö© Ïó¨Î∂Ä
   * @param allowAudio Ïò§ÎîîÏò§ ÏóÖÎ°úÎìú ÌóàÏö© Ïó¨Î∂Ä
   * @param allowFile ÏùºÎ∞ò ÌååÏùº ÏóÖÎ°úÎìú ÌóàÏö© Ïó¨Î∂Ä
   * @returns ÎπÑÌôúÏÑ±ÌôîÌï† ÌôïÏû• Í∏∞Îä• Î™©Î°ù
   */
  static getDisabledExtensions(
    userExtensions?: string[],
    allowVideo = false,
    allowAudio = false,
    allowFile = false
  ): string[] {
    const set = new Set<string>(userExtensions ?? []);
    if (!allowVideo) set.add("video");
    if (!allowAudio) set.add("audio");
    if (!allowFile) set.add("file");
    return Array.from(set);
  }
}

const createObjectUrlUploader = async (file: File): Promise<string> => {
  return URL.createObjectURL(file);
};

// ÌååÏùº ÌÉÄÏûÖ Í≤ÄÏ¶ù Ìï®Ïàò
const isImageFile = (file: File): boolean => {
  return (
    file.size > 0 &&
    (file.type?.startsWith("image/") ||
      (!file.type && /\.(png|jpe?g|gif|webp|bmp|svg)$/i.test(file.name || "")))
  );
};

export default function LumirEditor({
  // editor options
  initialContent,
  initialEmptyBlocks = 3,
  uploadFile,
  s3Upload,
  tables,
  heading,
  defaultStyles = true,
  disableExtensions,
  tabBehavior = "prefer-navigate-ui",
  trailingBlock = true,
  resolveFileUrl,
  allowVideoUpload = false,
  allowAudioUpload = false,
  allowFileUpload = false,
  // view options
  editable = true,
  theme = "light",
  formattingToolbar = true,
  linkToolbar = true,
  sideMenu = true,
  slashMenu = true,
  emojiPicker = true,
  filePanel = true,
  tableHandles = true,
  onSelectionChange,
  className = "",
  sideMenuAddButton = false,
  // callbacks / refs
  onContentChange,
}: LumirEditorProps) {
  // Ïù¥ÎØ∏ÏßÄ ÏóÖÎ°úÎìú Î°úÎî© ÏÉÅÌÉú
  const [isUploading, setIsUploading] = useState(false);
  const validatedContent = useMemo<DefaultPartialBlock[]>(() => {
    return ContentUtils.validateContent(initialContent, initialEmptyBlocks);
  }, [initialContent, initialEmptyBlocks]);

  // ÌÖåÏù¥Î∏î ÏÑ§Ï†ï Î©îÎ™®Ïù¥Ï†úÏù¥ÏÖò
  const tableConfig = useMemo(() => {
    return EditorConfig.getDefaultTableConfig(tables);
  }, [
    tables?.splitCells,
    tables?.cellBackgroundColor,
    tables?.cellTextColor,
    tables?.headers,
  ]);

  // Ìó§Îî© ÏÑ§Ï†ï Î©îÎ™®Ïù¥Ï†úÏù¥ÏÖò
  const headingConfig = useMemo(() => {
    return EditorConfig.getDefaultHeadingConfig(heading);
  }, [heading?.levels?.join(",") ?? ""]);

  // ÎπÑÌôúÏÑ±Ìôî ÌôïÏû• Î©îÎ™®Ïù¥Ï†úÏù¥ÏÖò
  const disabledExtensions = useMemo(() => {
    return EditorConfig.getDisabledExtensions(
      disableExtensions,
      allowVideoUpload,
      allowAudioUpload,
      allowFileUpload
    );
  }, [disableExtensions, allowVideoUpload, allowAudioUpload, allowFileUpload]);

  // S3 ÏóÖÎ°úÎìú ÏÑ§Ï†ï Î©îÎ™®Ïù¥Ï†úÏù¥ÏÖò (Í∞ùÏ≤¥ Ï∞∏Ï°∞ ÏïàÏ†ïÌôî)
  const memoizedS3Upload = useMemo(() => {
    return s3Upload;
  }, [
    s3Upload?.apiEndpoint,
    s3Upload?.env,
    s3Upload?.author,
    s3Upload?.userId,
    s3Upload?.path,
  ]);

  const editor = useCreateBlockNote<
    DefaultBlockSchema,
    DefaultInlineContentSchema,
    DefaultStyleSchema
  >(
    {
      initialContent: validatedContent as DefaultPartialBlock[],
      tables: tableConfig,
      heading: headingConfig,
      animations: false, // Í∏∞Î≥∏Ï†ÅÏúºÎ°ú Ïï†ÎãàÎ©îÏù¥ÏÖò ÎπÑÌôúÏÑ±Ìôî
      defaultStyles,
      // ÌôïÏû• ÎπÑÌôúÏÑ±: ÎπÑÎîîÏò§/Ïò§ÎîîÏò§/ÌååÏùº Ï†úÏñ¥
      disableExtensions: disabledExtensions,
      tabBehavior,
      trailingBlock,
      resolveFileUrl,
      uploadFile: async (file) => {
        // Ïù¥ÎØ∏ÏßÄ ÌååÏùºÎßå ÌóàÏö© (Ïù¥ÎØ∏ÏßÄ Ï†ÑÏö© ÏóêÎîîÌÑ∞)
        if (!isImageFile(file)) {
          throw new Error("Only image files are allowed");
        }

        try {
          let imageUrl: string;

          // 1. ÏÇ¨Ïö©Ïûê Ï†ïÏùò uploadFile Ïö∞ÏÑ†
          if (uploadFile) {
            imageUrl = await uploadFile(file);
          }
          // 2. S3 ÏóÖÎ°úÎìú (uploadFile ÏóÜÏùÑ Îïå)
          else if (memoizedS3Upload?.apiEndpoint) {
            const s3Uploader = createS3Uploader(memoizedS3Upload);
            imageUrl = await s3Uploader(file);
          }
          // 3. ÏóÖÎ°úÎìú Î∞©Î≤ïÏù¥ ÏóÜÏúºÎ©¥ ÏóêÎü¨
          else {
            throw new Error("No upload method available");
          }

          // BlockNoteÍ∞Ä ÏûêÎèôÏúºÎ°ú Ïù¥ÎØ∏ÏßÄ Î∏îÎ°ùÏùÑ ÏÉùÏÑ±ÌïòÎèÑÎ°ù URLÎßå Î∞òÌôò
          return imageUrl;
        } catch (error) {
          console.error("Image upload failed:", error);
          throw new Error(
            "Upload failed: " +
              (error instanceof Error ? error.message : String(error))
          );
        }
      },
      pasteHandler: (ctx) => {
        const { event, editor, defaultPasteHandler } = ctx as any;
        const fileList =
          (event?.clipboardData?.files as FileList | null) ?? null;
        const files: File[] = fileList ? Array.from(fileList) : [];
        const acceptedFiles: File[] = files.filter(isImageFile);

        // ÌååÏùºÏù¥ ÏûàÏßÄÎßå Ïù¥ÎØ∏ÏßÄÍ∞Ä ÏóÜÏúºÎ©¥ Í∏∞Î≥∏ Ï≤òÎ¶¨ ÎßâÍ≥† Î¨¥Ïãú
        if (files.length > 0 && acceptedFiles.length === 0) {
          event.preventDefault();
          return true;
        }

        // Ïù¥ÎØ∏ÏßÄÍ∞Ä ÏóÜÏúºÎ©¥ Í∏∞Î≥∏ Ï≤òÎ¶¨
        if (acceptedFiles.length === 0) {
          return defaultPasteHandler() ?? false;
        }

        event.preventDefault();
        (async () => {
          // Î∂ôÏó¨ÎÑ£Í∏∞Î°ú Ïó¨Îü¨ Ïù¥ÎØ∏ÏßÄ ÏóÖÎ°úÎìú Ïãú Î°úÎî© ÏÉÅÌÉú Í¥ÄÎ¶¨
          setIsUploading(true);
          try {
            for (const file of acceptedFiles) {
              try {
                // ÏóêÎîîÌÑ∞Ïùò uploadFile Ìï®Ïàò ÏÇ¨Ïö© (ÌÜµÏùºÎêú Î°úÏßÅ)
                const url = await editor.uploadFile(file);
                editor.pasteHTML(`<img src="${url}" alt="image" />`);
              } catch (err) {
                console.warn(
                  "Image upload failed, skipped:",
                  file.name || "",
                  err
                );
              }
            }
          } finally {
            setIsUploading(false);
          }
        })();
        return true;
      },
    },
    [
      validatedContent,
      tableConfig,
      headingConfig,
      defaultStyles,
      disabledExtensions,
      tabBehavior,
      trailingBlock,
      resolveFileUrl,
      uploadFile,
      memoizedS3Upload,
    ]
  );

  // Ìé∏Ïßë Í∞ÄÎä• Ïó¨Î∂Ä ÏÑ§Ï†ï
  useEffect(() => {
    if (editor) {
      editor.isEditable = editable;
    }
  }, [editor, editable]);

  // ÏΩòÌÖêÏ∏† Î≥ÄÍ≤Ω Í∞êÏßÄ
  useEffect(() => {
    if (!editor || !onContentChange) return;

    const handleContentChange = () => {
      // BlockNoteÏùò Ïò¨Î∞îÎ•∏ API ÏÇ¨Ïö©
      const blocks = editor.topLevelBlocks as DefaultPartialBlock[];
      onContentChange(blocks);
    };

    return editor.onEditorContentChange(handleContentChange);
  }, [editor, onContentChange]);

  // ÎìúÎûòÍ∑∏Ïï§ÎìúÎ°≠ Ïù¥ÎØ∏ÏßÄ Ï≤òÎ¶¨
  useEffect(() => {
    const el = editor?.domElement as HTMLElement | undefined;
    if (!el) return;

    const handleDragOver = (e: DragEvent) => {
      if (e.defaultPrevented) return;
      const hasFiles = (
        e.dataTransfer?.types as unknown as string[] | undefined
      )?.includes?.("Files");
      if (hasFiles) {
        e.preventDefault();
        e.stopPropagation();
      }
    };

    const handleDrop = (e: DragEvent) => {
      if (!e.dataTransfer) return;
      const hasFiles = (
        (e.dataTransfer.types as unknown as string[] | undefined) ?? []
      ).includes("Files");
      if (!hasFiles) return;

      e.preventDefault();
      e.stopPropagation();

      const items = Array.from(e.dataTransfer.items ?? []);
      const files = items
        .filter((it) => it.kind === "file")
        .map((it) => it.getAsFile())
        .filter((f): f is File => !!f);

      // Ïù¥ÎØ∏ÏßÄ ÌååÏùºÎßå ÌóàÏö©
      const acceptedFiles = files.filter(isImageFile);

      if (acceptedFiles.length === 0) return;

      (async () => {
        // ÎìúÎûòÍ∑∏Ïï§ÎìúÎ°≠ÏúºÎ°ú Ïó¨Îü¨ Ïù¥ÎØ∏ÏßÄ ÏóÖÎ°úÎìú Ïãú Î°úÎî© ÏÉÅÌÉú Í¥ÄÎ¶¨
        setIsUploading(true);
        try {
          for (const file of acceptedFiles) {
            try {
              // ÏóêÎîîÌÑ∞Ïùò uploadFile Ìï®Ïàò ÏÇ¨Ïö© (ÏùºÍ¥ÄÎêú Î°úÏßÅ)
              if (editor?.uploadFile) {
                const url = await editor.uploadFile(file);
                if (url) {
                  editor.pasteHTML(`<img src="${url}" alt="image" />`);
                }
              }
            } catch (err) {
              console.warn(
                "Image upload failed, skipped:",
                file.name || "",
                err
              );
            }
          }
        } finally {
          setIsUploading(false);
        }
      })();
    };

    el.addEventListener("dragover", handleDragOver, { capture: true });
    el.addEventListener("drop", handleDrop, { capture: true });

    return () => {
      el.removeEventListener("dragover", handleDragOver, {
        capture: true,
      } as any);
      el.removeEventListener("drop", handleDrop, { capture: true } as any);
    };
  }, [editor]);

  // SideMenu ÏÑ§Ï†ï (Add Î≤ÑÌäº Ï†úÏñ¥)
  const computedSideMenu = useMemo(() => {
    return sideMenuAddButton ? sideMenu : false;
  }, [sideMenuAddButton, sideMenu]);

  // Add Î≤ÑÌäº ÏóÜÎäî ÏÇ¨Ïù¥Îìú Î©îÎâ¥ (ÎìúÎûòÍ∑∏ Ìï∏Îì§Îßå) - Î©îÎ™®Ïù¥Ï†úÏù¥ÏÖò
  const DragHandleOnlySideMenu = useMemo(() => {
    return (props: any) => (
      <BlockSideMenu {...props}>
        <DragHandleButton {...props} />
      </BlockSideMenu>
    );
  }, []);

  return (
    <div
      className={cn("lumirEditor", className)}
      style={{ position: "relative" }}
    >
      <BlockNoteView
        editor={editor}
        editable={editable}
        theme={theme}
        formattingToolbar={formattingToolbar}
        linkToolbar={linkToolbar}
        sideMenu={computedSideMenu}
        slashMenu={false}
        emojiPicker={emojiPicker}
        filePanel={filePanel}
        tableHandles={tableHandles}
        onSelectionChange={onSelectionChange}
      >
        {slashMenu && (
          <SuggestionMenuController
            triggerCharacter="/"
            getItems={useCallback(
              async (query: string) => {
                const items = getDefaultReactSlashMenuItems(editor);
                // ÎπÑÎîîÏò§, Ïò§ÎîîÏò§, ÌååÏùº Í¥ÄÎ†® Ìï≠Î™© Ï†úÍ±∞
                const filtered = items.filter((item: any) => {
                  const key = (item?.key || "").toString().toLowerCase();
                  const title = (item?.title || "").toString().toLowerCase();
                  // ÎπÑÎîîÏò§, Ïò§ÎîîÏò§, ÌååÏùº Í¥ÄÎ†® Ìï≠Î™© Ï†úÍ±∞
                  if (["video", "audio", "file"].includes(key)) return false;
                  if (
                    title.includes("video") ||
                    title.includes("audio") ||
                    title.includes("file")
                  )
                    return false;
                  return true;
                });

                if (!query) return filtered;
                const q = query.toLowerCase();
                return filtered.filter(
                  (item: any) =>
                    item.title?.toLowerCase().includes(q) ||
                    (item.aliases || []).some((a: string) =>
                      a.toLowerCase().includes(q)
                    )
                );
              },
              [editor]
            )}
          />
        )}
        {!sideMenuAddButton && (
          <SideMenuController sideMenu={DragHandleOnlySideMenu} />
        )}
      </BlockNoteView>

      {/* Ïù¥ÎØ∏ÏßÄ ÏóÖÎ°úÎìú Î°úÎî© Ïä§ÌîºÎÑà */}
      {isUploading && (
        <div className="lumirEditor-upload-overlay">
          <div className="lumirEditor-spinner" />
        </div>
      )}
    </div>
  );
}
